<template>
    <div class="footerBottom">
        <div class="bottomBar">
            <div class="copyrightContent">
               <span><router-link  :to="{ name: 'privacy'}">{{$t('bottom.privacy')}}</router-link></span>
               <a>{{$t('bottom.copyright')}}</a>
            </div>
            <div class="newsletter">
              <a href="https://www.facebook.com/WennoWorld/"></a>
              <a href="https://vimeo.com/wenno"></a>
              <a href="https://www.instagram.com/WennoWorld/"></a>
              <a href="https://twitter.com/Wenno_World"></a>
              <a href="https://wennoworld.tumblr.com/"></a>
              <a href="https://ameblo.jp/wenno/"></a>
              <a href="https://www.pinterest.jp/wenno_world/"></a>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  name: "footerBottom",
  data () {
    return {
        name:'我要测试首页234'
    }
  }
};
</script>
<style>
.footerBottom{width:100%;height:70px;background:#fff6e9;}
.bottomBar{width:96%;height:70px;margin:0px auto;}
.copyrightContent{float:left;}
.copyrightContent span a{text-align:left;}
.newsletter{width:240px;height:70px;float:right;margin-right: 0px;}
@media screen and (max-width: 768px) {
.footerBottom{height:130px;}
.copyrightContent{width:100%;padding-top:6px;}
.copyrightContent span a{width:100%;text-align:center;display:block;}
.newsletter{width: 70%;height:60px;margin-right: 15%;}
}
</style>


