<template>
    <div class="productInfo">
        <div id="main">
           <div class="details">
               <div class="productBox">
                  <p>{{detailsArr.title}}</p>
                  <div class="animalBox">
                      <div class="parameter">
                         <div class="parameterLeft">
                             <span>{{detailsArr.number}}</span>
                             <span v-if="detailsArr.type=='0' ? false : true">{{detailsArr.position}}</span>
                         </div>
                         <div class="parameterRight">
                            <span>{{detailsArr.case}}</span>
                            <span>{{detailsArr.size}}</span>
                         </div>
                      </div>
                      <div class="animalImg">
                          <img :src="detailsArr.imgInfo">
                          <span>Product information</span>
                      </div>
                      <div class="quantity">
                         <span>12-piece Puzzle</span>
                         <span class="fourCenter" v-if="detailsArr.type=='0' ? false : true">4 Animal Figurines African Lion, Black Rhinoceros, Cheetah, Plains Zebra  </span>
                         <span class="center" v-if="detailsArr.type=='1' ? false : true">Jaguar Figurine</span>
                         <span class="center2" v-if="detailsArr.type=='1' ? false : true">Animal Facts Card</span>
                         <span>AR Game</span>
                      </div>
                  </div>
               </div>
           </div>
        </div>
        <Footer></Footer>
    </div>
</template>
<script>
import Footer from '@/components/footer'
export default {
  name: "productInfo",
  components:{
     Footer
  },
  data () {
    return {
        name:'我要测试首页',
        detailsArr:[]
    }
  },
  mounted:function(){ 
       window.scrollTo(0, 0);
  },
  methods:{
      getData(){
          let penga = this.$route.query.id;
          console.log(JSON.stringify(penga));
          this.detailsArr = penga;
      }
  },
  created:function(){
     this.getData();
  }
};
</script>
<style scoped>
   @import "../assets/css/productInfo.css";
</style>


