<template>
  <div class="productList">
      <div class="productCategory">
         <p class="categoryName">Animal Figurine with 12-piece Puzzle Play Set</p>
         <div class="swiper-container" refs="mySwiper" id="single">
             <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="(item,index)  in productBoxed">
                   <div class="productBox productRight" v-if="index%3==0" :index="index" @click="getDescribe(index,0)">
                      <img v-bind:src="item.img" v-on:error.once="moveErrorImg($event)">
                      <span>{{item.name}}</span>
                   </div>
                   <div class="productBox" v-if="index%3==1" :index="index" @click="getDescribe(index,0)">
                      <img :src="item.img">
                      <span>{{item.name}}</span>
                   </div>
                   <div class="productBox productLeft" v-if="index%3==2" :index="index" @click="getDescribe(index,0)">
                      <img :src="item.img">
                      <span>{{item.name}}</span>
                   </div>
                </div>
            </div>
            <div class="swiper-pagination"></div>
            <div class="swiper-button-next"></div>
            <div class="swiper-button-prev"></div>
        </div>  
     </div>
     <div class="productCategory evenList">
         <p class="categoryName">Animal Figurines with 4-piece Round Puzzle Play Set</p>
         <div class="swiper-container" refs="mySwiper" id="Boxed">
             <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="(item,index)  in productSingle">
                   <div class="productBox productRight" v-if="index%3==0" :index="index"  @click="getDescribe(index,1)">
                      <img :src="item.img">
                      <span>{{item.name}}</span>
                   </div>
                   <div class="productBox" v-if="index%3==1" :index="index" @click="getDescribe(index,1)">
                      <img :src="item.img">
                      <span>{{item.name}}</span>
                   </div>
                   <div class="productBox productLeft" v-if="index%3==2" :index="index" @click="getDescribe(index,1)">
                      <img :src="item.img">
                      <span>{{item.name}}</span>
                   </div>
                </div>
            </div>
            <div class="swiper-pagination"></div>
            <div class="swiper-button-next"></div>
            <div class="swiper-button-prev"></div>
        </div>  
     </div>
  </div>
</template>
<script>
export default {
  name: "productList",
  components:{
  },
  data () {
    return {
        name:'我要测试首页',
        productBoxed:[],
        productSingle:[],
        swiperoption:{
            slidesPerView: 3,
            spaceBetween:0,
             slidesPerGroup: 3,
            loop: true,
            loopFillGroupWithBlank: true,
            pagination: {
              el: '.swiper-pagination',
              clickable: true,
            },
            navigation: {
              nextEl: '.swiper-button-next',
              prevEl: '.swiper-button-prev',
            }          
        }
    }
  },
  watch: {
      productBoxed: function() {
          this.$nextTick(function(){
            /*现在数据已经渲染完毕*/
             var m;
             for(var i=0;i<=$('.swiper-container').length;i++){
                m = $('.swiper-container').eq(i).attr('id');
                var swiper = new Swiper('#'+m, this.swiperoption);
             }
            if (screen.width <= 768){
                $('.swiper-pagination-bullet').css({"height":"4px","width":"4px"});
             } else{
                return
             }
          }); 
      }    
  },
   methods:{
      getDescribe(index,status){
        if(status==0){
           var arr = this.productBoxed[index];
        }else{
           var arr = this.productSingle[index];
        }
         console.log(JSON.stringify(this.productBoxed[index]));
          this.$router.push({
             path: '/productInfo',
             query: {
                 id: arr
             }
          })
      },
      moveErrorImg:function (event) {
          event.currentTarget.src = "./images/product3.png";//默认图片
      },
      getData(){
          let self = this;  
          self.productBoxed = [
              {type:'0',title:'Animal Figurine with 12-piece Puzzle Play Set',id:0,img: 'http://47.75.178.168/uploads/product/single.png',imgInfo: 'http://47.75.178.168/uploads/product/singleInfo.png',name:'Jaguar Figurine',case:'12pcs/ 2.13cu.ft',size:'(L)6.75”x(W)5.75”x(H)2.125”',number:'WPZ0201',position:''},
              {type:'0',title:'Animal Figurine with 12-piece Puzzle Play Set',id:1,img: 'http://47.75.178.168/uploads/product/single1.png',imgInfo: 'http://47.75.178.168/uploads/product/singleInfo1.png',name:'African Lion Figurine',case:'12pcs/ 2.13cu.ft',size:'(L)6.75”x(W)5.75”x(H)2.125”',number:'WPZ0202',position:''},
              {type:'0',title:'Animal Figurine with 12-piece Puzzle Play Set',id:2,img: 'http://47.75.178.168/uploads/product/single2.png',imgInfo: 'http://47.75.178.168/uploads/product/singleInfo2.png',name:'Plains Zebra Figurine',case:'12pcs/ 2.13cu.ft',size:'(L)6.75”x(W)5.75”x(H)2.125”',number:'WPZ0203',position:''},
              {type:'0',title:'Animal Figurine with 12-piece Puzzle Play Set',id:3,img: 'http://47.75.178.168/uploads/product/single3.png',imgInfo: 'http://47.75.178.168/uploads/product/singleInfo3.png',name:'Giraffe Figurine',case:'12pcs/ 2.13cu.ft',size:'(L)6.75”x(W)5.75”x(H)2.125”',number:'WPZ0204',position:''},
              {type:'0',title:'Animal Figurine with 12-piece Puzzle Play Set',id:4,img: 'http://47.75.178.168/uploads/product/single4.png',imgInfo: 'http://47.75.178.168/uploads/product/singleInfo4.png',name:'Hippopotamus Figurine',case:'12pcs/ 2.13cu.ft',size:'(L)6.75”x(W)5.75”x(H)2.125”',number:'WPZ0205',position:''},
              {type:'0',title:'Animal Figurine with 12-piece Puzzle Play Set',id:5,img: 'http://47.75.178.168/uploads/product/single5.png',imgInfo: 'http://47.75.178.168/uploads/product/singleInfo5.png',name:'Siberian Tiger Figurine',case:'12pcs/ 2.13cu.ft',size:'(L)6.75”x(W)5.75”x(H)2.125”',number:'WPZ0206',position:''}
          ];  
          self.productSingle = [
              {type:'1',title:'Animal Figurines with 4-piece Round Puzzle Play Set',id:0,img: 'http://47.75.178.168/uploads/product/Boxed.png',imgInfo: 'http://47.75.178.168/uploads/product/BoxedInfo.png',name:'African Lion, Black Rhinoceros, Cheetah, Plains Zebra',case:'12pcs/ 2.13cu.ft',size:'(L)15”x(W)8”x(H)2.25”',number:'WPZ0101',position:'African Series'},
              {type:'1',title:'Animal Figurines with 4-piece Round Puzzle Play Set',id:1,img: 'http://47.75.178.168/uploads/product/Boxed1.png',imgInfo: 'http://47.75.178.168/uploads/product/BoxedInfo1.png',name:'African Elephant, African Lioness, Giraffe, Hippopotamus',case:'12pcs/ 2.13cu.ft',size:'(L)15”x(W)8”x(H)2.25”',number:'WPZ0102',position:'African Series'},
              {type:'1',title:'Animal Figurines with 4-piece Round Puzzle Play Set',id:2,img: 'http://47.75.178.168/uploads/product/Boxed2.png',imgInfo: 'http://47.75.178.168/uploads/product/BoxedInfo2.png',name:'Asian Elephant, Bactrian Camel, Giant Panda, Siberian Tiger',case:'12pcs/ 2.13cu.ft',size:'(L)15”x(W)8”x(H)2.25”',number:'WPZ0103',position:'Asian Series'},
              {type:'1',title:'Animal Figurines with 4-piece Round Puzzle Play Set',id:3,img: 'http://47.75.178.168/uploads/product/Boxed3.png',imgInfo: 'http://47.75.178.168/uploads/product/BoxedInfo3.png',name:'American Black Bear, Emu, Jaguar, Moose',case:'12pcs/ 2.13cu.ft',size:'(L)15”x(W)8”x(H)2.25”',number:'WPZ0104',position:'American Series'},
              {type:'1',title:'Animal Figurines with 4-piece Round Puzzle Play Set',id:4,img: 'http://47.75.178.168/uploads/product/Boxed4.png',imgInfo: 'http://47.75.178.168/uploads/product/BoxedInfo4.png',name:'Arctic: Arctic Wolf, Polar Bear, Reindeer; South Pole: Emperor Penguins)',case:'12pcs/ 2.13cu.ft',size:'(L)15”x(W)8”x(H)2.25”',number:'WPZ0105',position:'Polar Series'},
              {type:'1',title:'Animal Figurines with 4-piece Round Puzzle Play Set',id:5,img: 'http://47.75.178.168/uploads/product/Boxed5.png',imgInfo: 'http://47.75.178.168/uploads/product/BoxedInfo5.png',name:'Ankylosaurus, Diplodocus, Stegosaurus, Triceratops',case:'12pcs/ 2.13cu.ft',size:'(L)15”x(W)8”x(H)2.25”',number:'WPZ0106',position:'Herbivorous Dinosaurs'},
              {type:'1',title:'Animal Figurines with 4-piece Round Puzzle Play Set',id:6,img: 'http://47.75.178.168/uploads/product/Boxed6.png',imgInfo: 'http://47.75.178.168/uploads/product/BoxedInfo6.png',name:'Allosaurus, Dilophosaurus, Spinosaurus, Tyrannosaurus Rex',case:'12pcs/ 2.13cu.ft',size:'(L)15”x(W)8”x(H)2.25”',number:'WPZ0107',position:'Carnivorous Dinosaurs'}
          ];
      }
   },
   created:function(){
      this.getData();
   }
};

</script>
<style scoped>
.productCategory{width:100%;height:350px;}
.evenList{background:#fff6e9;}
.productCategory>p.categoryName{width:100%;height:78px;line-height:78px;text-align:center;color:#7b5c55;font-size:1.15rem;}
.swiper-container{width:86%;height:265px;}
.productBox{width:68%;height:210px;margin:10px auto;overflow:hidden;}
.productRight{margin:10px 0px 0px auto;}
.productLeft{float:left;margin-left:0px;}
.productBox img{height:176px;display:block;margin:0px auto;}
.productBox span{width:100%;display:block;height:40px;line-height:40px;font-size:0.9rem;text-align:center;color:#7b5c55;overflow: hidden;text-overflow: ellipsis;display: box;display: -webkit-box;-webkit-line-clamp:1;-webkit-box-orient: vertical;}
.swiper-button-prev {background: url(../assets/img/home_up.png)0% 50% no-repeat;background-size:46px 46px;width:46px;height: 46px;left:0px;}
.swiper-button-next {background: url(../assets/img/home_down.png)0% 50% no-repeat;background-size:46px 46px;width:46px;height: 46px;right:0px;}
@media screen and (max-width: 768px) {
 .swiper-button-prev {background: url(../assets/img/home_up.png)0% 50% no-repeat;background-size:20px 20px;width:20px;height:20px;left:0px;}
.swiper-button-next {background: url(../assets/img/home_down.png)0% 50% no-repeat;background-size:20px 20px;width:20px;height: 20px;right:0px;}  
.productCategory{width:100%;height:150px;}
.productCategory>p.categoryName{height:32px;line-height:32px;font-size:0.75rem;}
.swiper-container{width:86%;height:120px;}
.productBox{width:90%;height:80px;/*margin:10px auto;*/overflow:hidden;}
.productBox img{height:54px;display:block;margin:0px auto;}
.swiper-pagination-bullet{width:6px;height:6px;}
.productBox span{height:30px;line-height:30px;font-size:0.4rem;}
}
</style>


