import Vue from 'vue'
import Router from 'vue-router'
import index from '@/view/index'
import indexSwiper from '@/components/indexSwiper'
import indexVideo from '@/components/indexVideo'

import brand from '@/view/brand'

import Header from '@/components/header'
import Footer from '@/components/footer'

import home from '@/view/home'
import NewsSwiper from '@/components/newsSwiper'
import NewsList from '@/components/newsList'

import product from '@/view/product'
import productList from '@/components/productList'

import aboutUs from '@/view/aboutUs'
import aboutUsList from '@/components/aboutUsList'
import footerBottom from '@/components/footerBottom'

import contactUs from '@/view/contactUs'
import contactUsList from '@/components/contactUsList'

import retail from '@/view/retail'
import retailMap from '@/components/retailMap'

import productInfo from '@/view/productInfo'

import features from '@/view/features'
import featureList from '@/components/featureList'

import newView from '@/view/newView'
import newInfo from '@/components/newInfo'

import privacy from '@/view/privacy'
import privacyPolicy from '@/components/privacyPolicy'

Vue.use(Router)

export default new Router({
  routes: [
   {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/brand',
      name: 'brand',
      component: brand
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/product',
      name: 'product',
      component: product
    },
    {
      path: '/aboutUs',
      name: 'aboutUs',
      component: aboutUs
    },
    {
      path: '/contactUs',
      name: 'contactUs',
      component: contactUs
    },
    {
      path: '/retail',
      name: 'retail',
      component: retail
    },
    {
      path: '/productInfo',
      name: 'productInfo',
      component: productInfo
    },
    {
      path: '/features',
      name: 'features',
      component: features
    },
    {
      path: '/newView',
      name: 'newView',
      component: newView
    },
    {
      path: '/privacy',
      name: 'privacy',
      component: privacy
    }
  ]
})



