<template>
  <div class="newsInfo">
     <div class="newtitle">
        <div class="newBox">
           <p>{{title}}</p>
<!--            <img src="../assets/img/newTitle.png"> -->
        </div>
     </div>
     <div class="newContent" id="newContent" v-html="content">
           {{content}}  
     </div>
  </div>
</template>

<script>
export default {
  name: "newsInfo",  
  data () {
    return {
        name:'我要测试首页',
        title:'',
        content:'',
        url:'http://47.75.178.168/api/news/1'
    }
  },
  mounted:function(){ 
       window.scrollTo(0, 0);
  },
  methods: {
      dataRender () {
        let self = this;
        axios.get(self.url)
            .then(function (response) {
             // console.log(JSON.stringify(response.data.content));
              self.title = response.data.title;
              
              var s ='';
              s = response.data.content.replace(/&amp;/g,"&");
              s = s.replace(/&lt;/g,"<");
              s = s.replace(/&gt;/g,">");
              s = s.replace(/&nbsp;/g," ");
              s = s.replace(/&#39;/g,"\'");
              s = s.replace(/&quot;/g,"\"");
              s = s.replace(/margin/gi, "m");
              s = s.replace(/text-indent/gi, "t");
              s = s.replace(/padding/gi, "p");
              console.log(s);
              self.content = s;
            })
            .catch(function (error) {
                console.log(JSON.stringify(error));
            }) 
      }
  },
  created:function () {
     this.dataRender();
  }
};
</script>
<style scoped>
.newsInfo{background:#fff;width:100%;height:100%;color:#7b5c55;}
 .newtitle{background:#fff6e9;width:100%;height:auto;}  
 .newBox{width:76%;height:100%;margin:0px auto;}
 .newBox p{width:100%;line-height:80px;text-align:left;font-size:1rem;}
 .newBox img{width:100%;}
 .newContent{width:76%;background:#fff6e9;margin:30px auto;}
 .newContent span,.newContentBox span{line-height:24px;display:block;text-align:left;padding:1rem 0rem;width:100%;font-size:0.9rem;background:#fff;}
 .newContent img{max-width:100%;background:#fff6e9;}
 .newContentBox{width:76%;margin:0px auto;}
 .imgBox{width:100%;background:#fff6e9;}
 .imgBox img{max-width:100%;background:#fff6e9;}
 .newContentBox:last-child{margin-bottom:2rem;}
</style>



