           <span>It governs the privacy terms of our Wenno® Mobile Apps and Website, located at http://wennoanimal.com. Any capitalised terms not defined in our Privacy Policy, have the meaning as specified in our Terms of Service.</span>
           <p class="usListTitle">Your Privacy</p>
           <span>Wenno® follows all legal requirements to protect your privacy. Our Privacy Policy is a legal statement that explains how we may collect information from you, how we may share your information, and how you can limit our sharing of your information. You will see terms in our Privacy Policy that are capitalised. These terms have meanings as described in the Definitions section below.</span>
           <p class="usListTitle">Definitions</p>
           <span>“Non Personal Information” is information that is not personally identifiable to you and that we automatically collect when you access our Mobile Applications or Website with a web browser. It may also include publicly available information that is shared between you and others.<br>"Personally Identifiable Information" is non-public information that is personally identifiable to you and obtained in order for us to provide you within our Website. Personally Identifiable Information may include information such as your name, email address, and other related information that you provide to us or that we obtain about you.</span>
           <p class="usListTitle">Information We Collect</p>
           <span>Generally, you control the amount and type of information you provide to us when using our Website.<br>As a Visitor, you can browse our website to find out more about our Website. You are not required to provide us with any Personally Identifiable Information as a Visitor.</span>
           <p class="usListTitle">Computer Information Collected</p>
           <span>When you use our Website, we automatically collect certain computer information by the interaction of your mobile phone or web browser with our Website. Such information is typically considered Non Personal Information. We also collect the following:</span>
           <p class="usListTitle">Cookies</p>
           <span>Our Website uses “Cookies” to identify the areas of our Website that you have visited. A Cookie is a small piece of data stored on your computer or mobile device by your web browser. We use Cookies to personalize the Content that you see on our Website. Most web browsers can be set to disable the use of Cookies. However, if you disable Cookies, you may not be able to access functionality on our Website correctly or at all. We never place Personally Identifiable Information in Cookies.</span>
           <p class="usListTitle">Automatic Information</p>
           <span>We automatically receive information from your web browser or mobile device. This information includes the name of the website from which you entered our Website, if any, as well as the name of the website to which you’re headed when you leave our website. This information also includes the IP address of your computer/proxy server that you use to access the Internet, your Internet Website provider name, web browser type, type of mobile device, and computer operating system. We use all of this information to analyse trends among our Users to help improve our Website.</span>
           <p class="usListTitle">Third Party Services</p>
           <span>Wenno® does not intend to transfer Personal Information without your consent to third parties who are not bound to act on Wenno® behalf unless such transfer is legally required. Similarly, it is against Wenno® policy to sell Personal Information collected online without consent.</span>
