           <span>Wenno® will not contact children under age 13 about special offers or for marketing purposes without a parent's permission.</span>
           <p class="usListTitle">Information Sharing and Disclosure</p>
           <span>Wenno® does not rent, sell, or share personal information about you with other people (save with your consent) or non-affiliated companies except to provide products or services you've requested, when we have your permission, or under the following circumstances We provide the information to trusted partners who work on behalf of or with Wenno® under confidentiality agreements. These companies may use your personal information to help Wenno® communicate with you about offers from Wenno® and our marketing partners. However, these companies do not have any independent right to share this information.<br>
           We respond to subpoenas, court orders, or legal process, or to establish or exercise our legal rights or defend against legal claims;<br>
           We believe it is necessary to share information in order to investigate, prevent, or take action regarding illegal activities, suspected fraud, situations involving potential threats to the physical safety of any person, violations of Wenno® terms of use, or as otherwise required by law.</span>
           <span>We transfer information about you if Wenno® is acquired by or merged with another company. In this event, Wenno® will notify you before information about you is transferred and becomes subject to a different privacy policy.<br>
